import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  AppBar,
  Toolbar,
  MenuItem,
  Menu,
  Badge,
  IconButton,
  Button,
  Switch,
} from "@material-ui/core";

import NotificationsIcon from "@material-ui/icons/Notifications";
import PermIdentityIcon from "@material-ui/icons/PermIdentity";

import Sidebar from "components/Sidebar/Sidebar";

const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  navbar: {
    marginBottom: theme.spacing(2),
  },
}));

const Navbar2 = () => {
  //const [date, setDate] = React.useState(today.split(" "));

  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);

  const [sidebarState, setSidebarState] = React.useState(false);

  const handleDrawerToggle = () => {
    setSidebarState(!sidebarState);
  };

  const isMenuOpen = Boolean(anchorEl);
  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      //anchorOrigin={{ vertical: "top", horizontal: "right" }}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem>내정보</MenuItem>
      <MenuItem>
        알림 설정
        <Switch
          checked={true}
          //onChange={handleChange}
          name="checkedB"
          color="primary"
        />
      </MenuItem>
      <MenuItem>로그아웃</MenuItem>
    </Menu>
  );

  return (
    <div className={(classes.grow, classes.navbar)}>
      <AppBar position="static">
        <Toolbar>
          <IconButton
            edge="start"
            color="inherit"
            className={classes.menuButton}
            onClick={handleDrawerToggle}
          >
            <Badge badgeContent={17} color="secondary">
              <NotificationsIcon />
            </Badge>
          </IconButton>
          <Button color="inherit">쇼핑내역</Button>
          <Button color="inherit">쇼핑노트</Button>
          <Button color="inherit">쇼핑통계</Button>

          <div className={classes.grow} />

          <IconButton
            aria-label="show more"
            aria-haspopup="true"
            onClick={handleMenuOpen}
            color="inherit"
          >
            <PermIdentityIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <Sidebar
        openState={sidebarState}
        handleDrawerToggle={handleDrawerToggle}
      />
      {renderMenu}
    </div>
  );
};

export default Navbar2;
