import React from "react";
import { Container, Divider, Grid, IconButton } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import AddCircleIcon from "@material-ui/icons/AddCircle";

import SearchIcon from "@material-ui/icons/Search";

const useStyles = makeStyles({
  footer: {
    position: "fixed",
    width: "100%",
    bottom: "0px",
    display: "flex",
    height: "70px",
  },
});
const History = () => {
  const classes = useStyles();
  return (
    <div>
      <Container style={{ height: "100%", textAlign: "center" }}>
        <Grid container>
          <Grid item>
            <h3>나의 쇼핑 이력</h3>
          </Grid>
        </Grid>
        <Divider />
        <Grid style={{ height: "100%" }}>
          <Grid item xs={12} style={{ height: "100%", marginTop: "100px" }}>
            AAAA <br />
            <br />
            <br />
            <br />
            AAAAAAAA
          </Grid>
        </Grid>
      </Container>
      <div className={classes.footer}>
        <div style={{ flex: 1 }}>
          <IconButton style={{ marginLeft: "20px" }}>
            <SearchIcon fontSize="large"></SearchIcon>
          </IconButton>
        </div>
        <div>
          <IconButton style={{ marginRight: "20px" }}>
            <AddCircleIcon fontSize="large"></AddCircleIcon>
          </IconButton>
        </div>
      </div>
    </div>
  );
};

export default History;
