import React from "react";

import KeyboardBackspaceIcon from "@material-ui/icons/KeyboardBackspace";
import {
  Container,
  Divider,
  Grid,
  TextField,
  IconButton,
} from "@material-ui/core";

const AddHistory = () => {
  return (
    <div>
      <div style={{ display: "flex" }}>
        <IconButton>
          <KeyboardBackspaceIcon />
        </IconButton>
        <h4>쇼핑 이력 등록</h4>
      </div>
      <div style={{ display: "flex" }}>
        <h4>날짜</h4>
        <TextField
          label="Search input"
          name="searchInput"
          variant="outlined"
          style={{ margin: "auto 0" }}
        ></TextField>
      </div>
    </div>
  );
};

export default AddHistory;
